import React,{Component} from 'react';
import {TabBar} from 'antd-mobile';
import 'antd-mobile/dist/antd-mobile.css';
import chat1 from '../../assert/image/chat1.png'
import chat0 from '../../assert/image/chat0.png'
import file1 from '../../assert/image/document1.png'
import file0 from '../../assert/image/document0.png'
import briefcase0 from '../../assert/image/briefcase0.png'
import briefcase1 from '../../assert/image/briefcase1.png'



export default class HomeTabBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // selectedTab: 'tab1',
            hidden: false,
            fullScreen: true,
        };
    }


    render() {
        return (

            <div className="homeTabBar" style={this.state.fullScreen ? {
                position: 'fixed',
                height: '93%',
                width: '100%',
                bottom: 0
            } : {height: '100%'}}>

                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white"
                >
                    <TabBar.Item

                        key='chatContent'
                        icon={<div style={{
                            width: '30px',
                            height: '30px',
                            background: `url(${chat1}) center center /  29px 29px no-repeat`

                        }}
                        />
                        }
                        selectedIcon={<div style={{
                            width: '30px',
                            height: '30px',
                            background: `url(${chat0}) center center /  29px 29px no-repeat`
                        }}
                        />
                        }
                        // selected={this.state.selectedTab === 'tab1'}

                        // onPress={() => {
                        //     this.setState({
                        //         selectedTab: 'tab1',
                        //     });
                        // }}
                        selected={this.props.selectedTab==='tab1'}
                        onPress={()=>this.props.onTabPress('tab1')}
                    >
                    {/*第一页内容*/}
                        {this.props.chatPage}

                    </TabBar.Item>

                    <TabBar.Item

                        key='documentContent'
                        icon={<div style={{
                            width: '30px',
                            height: '30px',
                            background: `url(${file1}) center center /  29px 29px no-repeat`

                        }}
                        />
                        }
                        selectedIcon={<div style={{
                            width: '30px',
                            height: '30px',
                            background: `url(${file0}) center center /  29px 29px no-repeat`
                        }}
                        />
                        }
                        // selected={this.state.selectedTab === 'tab2'}
                        //
                        // onPress={() => {
                        //     this.setState({
                        //         selectedTab: 'tab2',
                        //     });
                        // }}

                        selected={this.props.selectedTab==='tab2'}
                        onPress={()=>this.props.onTabPress('tab2')}
                    >

                        {/*第二页内容*/}
                        {this.props.cloudPage}

                    </TabBar.Item>


                    <TabBar.Item

                        key='mySelf'
                        icon={<div style={{
                            width: '30px',
                            height: '30px',
                            background: `url(${briefcase1}) center center /  29px 29px no-repeat`

                        }}
                        />
                        }
                        selectedIcon={<div style={{
                            width: '30px',
                            height: '30px',
                            background: `url(${briefcase0}) center center /  29px 29px no-repeat`
                        }}
                        />
                        }
                        // selected={this.state.selectedTab === 'tab3'}
                        //
                        // onPress={() => {
                        //     this.setState({
                        //         selectedTab: 'tab3',
                        //     });
                        // }}

                        selected={this.props.selectedTab==='tab3'}
                        onPress={()=>this.props.onTabPress('tab3')}
                    >
                    {/*第三页内容*/}
                        {this.props.briefcasePage}

                    </TabBar.Item>


                </TabBar>
            </ div>
        )
    }
}



