
import React,{ Component } from 'react';
import HomeHeader from '../../component/HomeHeader/HomeHeader';
import HomeTabBar from '../../component/HomeTabBar/HomeTabBar';
import ChatContent from "../../component/ChatContent/ChatContent";
import {connect} from 'react-redux'
import {switchTab} from "../../store/home/action";



 class Home extends Component {

    render() {
        const {dispatch,tabNumber,visibilityStickerContents}=this.props
        return (
           <div className='homeApp'>
               <HomeHeader
                   selectedTab={tabNumber}
               />
               <HomeTabBar
                   onTabPress={number=>dispatch(switchTab(number))}
                   selectedTab={tabNumber}
                   chatPage={<ChatContent stickerContents={visibilityStickerContents} />}
               />
           </div>
        )
    }
}

function select(state){
    return{
        tabNumber:state.tabNumber,
        visibilityStickerContents:state.stickerContents
    }
}


export default connect(select)(Home);


