import React,{Component} from 'react'
import './Login.css'


export default class Login extends Component{
    constructor(props){
        super(props);
        this.state = {
            userName: "",
            password: ""
        }
    }


    handleUserNameChange(e){

            this.setState({userName: e.target.value})

    }

    handlePasswordChange(e){

        this.setState({password:e.target.value})
    }

    handleClick(e){

    }

    render(){
        return(
            <div className='login'>
                <img src='https://zos.alipayobjects.com/rmsportal/dKbkpPXKfvZzWCM.png' alt="" />

                    <input placeholder="QQ号/手机号/邮箱"  className='loginUserName' type='text' onChange={(e)=>this.handleUserNameChange(e)} value={this.state.userName}/>

                    <input placeholder='密码' className="loginPassword" type='password' onChange={(e)=>this.handlePasswordChange(e)} value={this.state.password}/>

                    <button type='button' onClick={(e)=>this.handleClick(e)} >登录</button>

                <a className="loginA1">新用户注册</a>
                <a className="loginA2">无法登陆？</a>
            </div>
        )
    }
}


