import React,{Component} from 'react'
import './RecentSticker.css'
import {Badge} from 'antd-mobile';
import 'antd-mobile/dist/antd-mobile.css';


export default class RecentSticker extends Component{
    render(){
        return(
            <div className='recentSticker'>
                <img className='timHead' src={this.props.timHeadUrl} alt=''/>

                <div className='recentFriendName' >
                    {this.props.recentFriendName}
                </div>

                <div className='previousMessage'>
                    {this.props.previousMessage}
                </div>

                <div className='previousMessageTime'>
                    {this.props.previousMessageTime}
                </div>

                <Badge className='unreadMessageBadge' text={this.props.unreadMessageBadge} overflowCount={99} />

            </div>
        )
    }
}


